
import Toast from 'vux/src/components/toast/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

const json = {code: 'b1'}
export default {
name: '',
components: {
Toast,
Icon,
XButton
},
data () {
return {
ck: true,
banner: '',
toastData: {
isShow: false,
type: 'warn',
text: '请选择您要试用的产品'
},
lists: []
}
},
created () {
this.axios.post('/web/adlist', json).then((result) => {
if (result.success) {
this.banner = this.base_img + result.data[0].img
}
this.lists.map((item, index) => {
item.checked = true
})
this.lists = []
console.log(result)
})
},
methods: {
check (item, index) {
console.log(item, index)
this.lists.filter((item1, index1) => {
if (index === index1) {
console.log(index, index1)
item1.checked = !item1.checked
}
})
console.log(this.lists)
},
buy () {
this.toastData.isShow = true
}
}
}



